* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  font-family: Arial, Helvetica, sans-serif;
  font-size: calc(10px + 2vmin);
  min-height: 100vh;
  width: 100%;
  z-index: -99;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: -webkit-gradient(linear, left bottom, right top, from(#b7dcf4), to(#fafcfc));
  background: linear-gradient(to top right, #b7dcf4, #fafcfc); }

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.fadeIn {
  -webkit-animation: fadeIn 0.5s ease;
          animation: fadeIn 0.5s ease; }

.loader-container {
  display: flex; }

.circle {
  display: block;
  width: 10px;
  height: 10px;
  border-radius: 100%;
  padding: 10px;
  margin: 10px;
  background-color: #2b50c5; }

.square {
  display: block;
  width: 10px;
  height: 10px;
  padding: 10px;
  margin: 10px;
  background-color: #2b50c5; }

.triangle {
  border: 20px solid;
  border-left-color: transparent;
  border-right-color: transparent;
  border-top: none;
  border-bottom-color: #2b50c5;
  border-bottom-width: calc(60 * 0.866%);
  margin: 0; }

.card-container {
  margin: 20px auto;
  z-index: -10;
  width: 100%;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fafcfc;
  box-shadow: 10px 15px 20px #242424;
  border: 20px solid #f8c3b9;
  width: 90%; }
  @media screen and (min-width: 601px) {
    .card-container {
      width: 50%;
      min-width: 600px;
      max-width: 960px; } }
  .card-container .card {
    z-index: 50;
    display: flex;
    flex-direction: column;
    align-content: center;
    position: relative;
    text-align: center;
    width: 100%;
    min-height: 80vh;
    background-color: #fafcfc; }
    .card-container .card h1 {
      font-family: "Amatic SC", sans-serif;
      font-size: 84px;
      color: #dcbe60; }

h2 {
  font-family: "Coming Soon", cursive;
  font-size: calc(0.5rem + 2vmin); }

