    color: #000;
    opacity: 1;
    /* Firefox */ }
  input:-ms-input-placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #000;
    opacity: 1;
    /* Firefox */ }
  input::-ms-input-placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #000;
    opacity: 1;
    /* Firefox */ }
  input::placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #000;
