  @media screen and (min-width: 601px) {
    input {
      margin: 10px auto;
      font-size: calc(0.3rem + 1vmin);
      outline: none; } }

.datepicker {
  font-family: "Amatic SC", sans-serif;
  color: #2b50c5;
  font-size: calc(2rem + 4vmin);
  width: 100% !important;
  border: none; }

label {
  font-family: "Amatic SC", sans-serif;
  color: #2b50c5;
  font-size: calc(1rem + 4vmin);
  margin-bottom: 0;
  padding-bottom: 0; }

.btn {
  display: inline-block;
  justify-self: center;
  align-self: center;
  font-size: 16px;
  font-family: "Coming Soon", cursive;
  padding: 10px 20px;
  margin-top: 2rem;
  border: 2px solid #2b50c5;
  color: #ffffff;
  background: #2b50c5;
  -webkit-transition: 0.2s ease;
  transition: 0.2s ease; }
  .btn:hover, .btn:active {
