    background-color: #2b50c5;
    color: #ffffff;
    box-shadow: 0 3px 10px #242424; }

.background-wrapper {
  width: 100%;
  max-height: 100%;
  position: absolute;
  overflow: hidden;
  bottom: 0;
  left: 0;
  z-index: -5; }
  .background-wrapper svg {
    width: 90%; }

.table-container {
